@use "../abstracts/variables" as *;
@use "../abstracts/autoprefixer" as *;

.bg-color-1 {
  background: $color-lblue2;
}

/*=========================
    16 Pament Get Card
===========================*/
.body-bg-1,
.body-bg-2,
.body-bg-3 {
  background-repeat: no-repeat;
  background-size: cover;
}
.body-bg-1,
.body-bg-2 {
  background-position: top;
}
.body-bg-3 {
  background-position: 0px 250px;
}
.body-bg-1 {
  padding-bottom: 400px;
  & ~ .mt-shap-over {
    margin-top: -400px;
  }
}
.body-bg-2,
.body-bg-3 {
  padding-top: 330px;
  margin-top: -370px;
}
.pamentget-card {
  &.v1 {
    .pament-card-img {
      margin-left: -25px;
      padding-right: 80px;
      img {
        width: 100%;
      }
    }
    .pay-catch-card {
      padding: 40px 32px 32px 32px;
      border-radius: 16px;
      box-shadow: 0px 2.767px 2.214px 0px rgba(0, 0, 0, 0.02),
        0px 6.65px 5.32px 0px rgba(0, 0, 0, 0.03),
        0px 12.522px 10.017px 0px rgba(0, 0, 0, 0.04),
        0px 22.336px 17.869px 0px rgba(0, 0, 0, 0.04),
        0px 41.778px 33.422px 0px rgba(0, 0, 0, 0.05),
        0px 100px 80px 0px rgba(0, 0, 0, 0.07);
      background: $color-white;
      margin-left: 110px;
    }
    .pay-catch-header {
      .title {
        font-size: 20px;
        line-height: 30px;
        font-weight: 700;
        & ~ .para {
          margin-top: 8px;
        }
      }
      .para {
        font-size: 16px;
        line-height: 26px;
        color: $color-gBlack4;
      }
      & ~ .pay-catch-body {
        margin-top: 40px;
      }
    }
    .pay-catch-btns {
      ul {
        @include d-flex;
        @include align-items;
      }
      li {
        width: 50%;
        &:nth-child(1) button::before {
          right: 0;
        }
        &:nth-child(2) button::before {
          left: 0;
        }
      }
      button {
        position: relative;
        padding-bottom: 18px;
        width: 100%;
        font-size: 16px;
        font-weight: 700;
        color: $color-gBlack4;
        border-bottom: 2px solid $color-gBlack5;
        background: $color-tr;
        &::before {
          position: absolute;
          content: "";
          width: 0%;
          height: 3px;
          bottom: -2px;
          opacity: 0;
          background: $color-blue3;
          box-shadow: 0px 5px 10px 0px rgba(0, 0, 0, 0.2);
          @include transition;
        }
        &.active {
          color: $color-black;
          &::before {
            opacity: 1;
            width: 100%;
          }
        }
      }
    }
    .tab-content {
      padding-top: 24px;
    }
    .items-cards,
    .content-uplode,
    .content-uplode .icon-text {
      @include d-flex;
      @include align-items;
      @include flex-wrap;
    }
    .items-cards {
      gap: 16px 8px;
      li {
        padding: 31px;
        border: 1px solid $color-gBlack2;
        border-radius: 8px;
        background: $color-white;
        &.small-card {
          width: calc(50% - 4px);
          box-shadow: 0px 33px 22px 0px rgba(203, 220, 212, 0.17);
        }
        &.big-card {
          width: 100%;
          padding: 22px 31px 31px 31px;
        }
      }
      .icon-tag {
        @include d-flex;
        @include align-items;
        @include justify-content-between;
        @include flex-wrap;
        gap: 16px;
        i {
          font-size: 26px;
        }
        .tag {
          display: inline-block;
          font-size: 14px;
          text-align: center;
          font-weight: 600;
          border-radius: 16px;
          min-width: 83px;
          &.v1 {
            padding: 4px 12px;
            color: $color-white;
            background: $color-black;
          }
          &.v2 {
            padding: 2px 12px;
            color: $color-black;
            border: 1px solid $color-blue4;
            background: $color-blue3;
          }
        }
        & ~ .para {
          margin-top: 33px;
        }
      }
      .para {
        font-size: 14px;
        line-height: 24px;
        color: $color-gBlack4;
      }
      & ~ .pay-btn {
        margin-top: 24px;
      }
    }
    .content-uplode {
      @include justify-content-between;
      gap: 15px;
    }
    .text-content {
      .small-title {
        font-size: 16px;
        line-height: 26px;
        font-weight: 600;
        color: $color-black;
        & ~ .main-title {
          margin-top: 6px;
        }
      }
      .main-title {
        font-size: 24px;
        line-height: 34px;
        font-weight: 700;
        & ~ .icon-text {
          margin-top: 25px;
        }
      }
      .icon-text {
        gap: 4px;
        i,
        .text {
          color: $color-gBlack4;
        }
        i {
          font-size: 16px;
        }
        .text {
          font-size: 12px;
          line-height: 22px;
        }
      }
    }
    .uplode-fild {
      padding: 34px 30px 30px 30px;
      text-align: center;
      border-radius: 4px;
      background: $color-white3;
      input {
        display: none;
      }
      .uplode-title {
        font-size: 12px;
        line-height: 22px;
        font-weight: 500;
        color: $color-black;
        & ~ label {
          margin-top: 8px;
        }
      }
      label {
        cursor: pointer;
        font-size: 24px;
        color: $color-gBlack4;
        @include transition;
        &:hover {
          color: $color-blue4;
        }
      }
    }
    .pay-btn {
      display: block;
      padding: 22px;
      width: 100%;
      font-size: 16px;
      font-weight: 500;
      border-radius: 8px;
      background: $color-blue4;
      i {
        margin-right: 4px;
      }
      &:hover {
        color: $color-white;
      }
    }
  }
}

/*=========================
    17 Invoices Card
===========================*/

.invoices {
  &.v1 {
    .invoices-card {
      padding: 86px 60px 75px 60px;
      border-radius: 15px;
      box-shadow: 0px 33px 22px 0px rgba(203, 220, 212, 0.17);
      background-color: $color-white;
      margin-top: 30px;
      @include transition;
      &.bg-img-cover {
        background-repeat: no-repeat;
        background-position: bottom;
        background-size: cover;
      }
      .invoices-card-head {
        @include d-flex;
        @include justify-content-between;
        @include align-items;
        @include flex-wrap;
        & ~ .card-title {
          margin-top: 38px;
        }
      }
      .numbber-stitle {
        text-align: right;
      }
      .count-num,
      .stitle {
        font-size: 16px;
        font-weight: 600;
      }
      .count-num {
        color: $color-gBlack4;
        @include transition;
      }
      .stitle {
        color: $color-black;
      }
      .card-title {
        font-weight: 500;
        letter-spacing: -0.56px;
        & ~ .card-para {
          margin-top: 38px;
        }
      }
      .card-para {
        font-size: 16px;
        font-weight: 500;
        line-height: 25px;
        color: $color-black;
      }
      &.invoices-card-small {
        .card-title {
          font-size: 28px;
          line-height: 36px;
        }
      }
      &.invoices-card-big {
        .card-title {
          font-size: 48px;
          line-height: 58px;
        }
      }
      &:hover {
        background-color: $color-yellow;
        .count-num {
          color: $color-black;
        }
        .read-more::before {
          background: $color-white;
        }
      }
    }
    .row > div:nth-child(1) .invoices-card,
    .row > div:nth-child(2) .invoices-card-small {
      margin-top: 0;
    }
  }
}

/*=========================
    18 Financial
===========================*/

.financial {
  &.v1 {
    .financial-contant {
      @include d-flex;
      @include justify-content-between;
      gap: 30px;
    }
    .chart-img-content {
      max-width: 475px;
      .chart-img {
        max-width: 330px;
        img {
          width: 100%;
          border-radius: 16px;
        }
        & ~ .contant-para {
          margin-top: 32px;
        }
      }
      .contant-para {
        font-size: 18px;
        font-weight: 600;
        line-height: 30px;
        letter-spacing: -0.18px;
        color: $color-black;
        & ~ .contant-btn-link {
          margin-top: 48px;
        }
      }
      .contant-btn-link {
        @include d-flex;
        @include align-items;
        gap: 16px;
        .contant-title {
          font-size: 24px;
          font-weight: 500;
          letter-spacing: -0.48px;
        }
      }
    }
    .dollar-img {
      max-width: 215px;
      img {
        width: 100%;
      }
    }
  }
}

/*=========================
    19 Why They Prefer
===========================*/

.why-they-prefer {
  &.v1 {
    .quick-support-img,
    .the-world-box,
    .digital-visa-card-img {
      overflow: hidden;
      box-shadow: 10px 33px 22px 0px rgba(203, 220, 212, 0.17);
      img {
        width: 100%;
        @include transition;
      }
    }
    .quick-support-img,
    .digital-visa-card-img {
      position: relative;
      border-radius: 32px;
      .quick-para {
        position: absolute;
        @include d-flex;
        @include align-items;
        padding: 25px 60px;
        gap: 12px;
        font-size: 18px;
        left: 50%;
        font-weight: 600;
        border-radius: 16px;
        text-align: center;
        text-transform: uppercase;
        white-space: nowrap;
        @include transform(translateX(-50%));
        @include transition;
        opacity: 0;
        visibility: hidden;
      }
      &:hover {
        .quick-para {
          opacity: 1;
          visibility: visible;
        }
        img {
          @include transform(scale(1.1));
        }
      }
    }
    .the-world-box {
      padding: 65px 50px;
      border-radius: 24px;
      text-align: center;
      background: $color-white;
      .text-content {
        .text-numbber {
          font-size: 90px;
          line-height: 110px;
        }
        .text-para {
          font-size: 16px;
          line-height: 26px;
          font-weight: 600;
          color: $color-black;
        }
        & ~ img {
          margin-top: 40px;
        }
      }
    }
    .quick-support-img {
      .quick-para {
        bottom: 44px;
        background: $color-white;
      }
    }
    .digital-visa-card-img {
      .quick-para {
        top: 56px;
        background: $color-lblue2;
      }
    }
  }
}

/*=========================
    20 Discover
===========================*/

.discover {
  &.v1 {
    position: relative;
    .bg-right-img {
      position: absolute;
      top: 354px;
      right: 80px;
    }
    .discover-left-contant {
      @include d-flex;
      @include align-items;
      @include justify-content-between;
      @include flex-wrap;
      gap: 30px;
      background: $color-blue3;
      padding: 45px 32px 45px 40px;
      border-radius: 10px;
      .team-images {
        @include d-flex;
        li {
          & ~ li {
            margin-left: -22px;
          }
          img {
            width: 64px;
            height: 64px;
            border-radius: 100%;
            border: 4px solid $color-white;
            @include transition;
          }
          &:hover {
            z-index: 1;
          }
        }
      }
      & ~ .features-accordion {
        margin-top: 68px;
      }
    }
    .discover-right-img {
      position: relative;
      img {
        border-radius: 32px;
        width: 100%;
      }
      .right-shap {
        position: absolute;
        width: 100%;
        height: 60%;
        bottom: 10%;
        right: 0;
        background-repeat: no-repeat;
        background-size: contain;
        background-position: center;
      }
    }
    .features-accordion {
      li {
        padding: 40px 65px 37px 40px;
        border-radius: 0px;
        border-bottom: 1px solid $color-gBlack7;
        @include transition;
        &.active {
          border-radius: 10px;
          border-bottom: 1px solid $color-tr;
          box-shadow: 0px 33px 22px 0px rgba(203, 220, 212, 0.17);
          background: $color-white;
          button i {
            color: $color-blue4;
          }
        }
        .box-content {
          padding: 25px 0px 0px 45px;
          p {
            font-size: 16px;
            line-height: 26px;
          }
        }
      }
      button {
        position: relative;
        font-size: 20px;
        font-weight: 600;
        width: 100%;
        text-align: inherit;
        background: $color-tr;
        &::after {
          position: absolute;
          font-family: "icomoon";
          content: "\e901";
          speak: never;
          right: -25px;
          top: 0;
          font-size: calc(100% - 2px);
          font-style: normal;
          font-weight: normal;
          font-variant: normal;
          text-transform: none;
          line-height: 1;
          -webkit-font-smoothing: antialiased;
          -moz-osx-font-smoothing: grayscale;
          @include transition;
        }
        i {
          color: $color-gBlack6;
          margin-right: 20px;
          @include transition;
        }
        &:not(.collapsed) {
          &::after {
            @include transform(rotate(180deg));
          }
        }
        &:hover {
          color: $color-blue4;
        }
      }
    }
  }
}

/*=========================
    21 Library
===========================*/

.library {
  &.v1 {
    .library-card {
      position: relative;
      border-radius: 32px;
      z-index: 1;
      .library-card-header ~ .talk-para {
        margin-top: 133px;
      }
      .library-card-header ~ .guides-title {
        margin-top: 154px;
      }
      .bg-shap-img {
        position: absolute;
        width: 54%;
        height: 75%;
        background-repeat: no-repeat;
        background-size: contain;
        background-position: center;
        z-index: -1;
      }
      &.v1 {
        padding: 50px 58px 48px 40px;
        background: $color-blue3;
        .library-card-header {
          @include d-flex;
          @include justify-content-between;
          @include align-items;
          & ~ .talk-para {
            padding-right: 145px;
          }
        }
        .talk-title {
          font-size: 55px;
          font-weight: 500;
        }
        .talk-para {
          font-size: 24px;
          font-weight: 600;
          line-height: 34px;
          color: $color-black;
        }
        .bg-shap-img {
          right: 10px;
          top: 25px;
        }
      }
      &.v2 {
        padding: 40px 58px 46px 48px;
        background: $color-yellow;
        .tag {
          display: inline-block;
          border: 1px solid $color-blue;
          font-size: 14px;
          text-align: center;
          font-weight: 600;
          border-radius: 16px;
          min-width: 83px;
          padding: 2px 12px;
          color: $color-white;
          background: $color-black;
        }
        .guides-title {
          font-size: 35px;
          font-weight: 500;
        }
        .bg-shap-img {
          right: 0;
          top: 21px;
        }
      }
    }
  }
}
